import React from "react";
import "./contact.css"

class Contact extends React.Component{
    render(){
        return(
            <section id="contactSection">
                <h1>Subsribe Newsletter</h1>
                <p>Lorem Ipsum has been the industry's standard </p>
                <div className="inputSubmitContainer">
                    <form>                    
                        <input type="text" id="email" name="email" value="Enter your email here"/>
                    </form>
                    <button className="submitButton">Get Started</button>
                </div>
            </section>
        )
    }
};

export default Contact;

