import React from "react";
import './about.css';
import cardPic from "../../assets/pexels-fox-3829230.jpg";
class About extends React.Component{
    render(){
        return(
            <section id="aboutSection">
                <div id="topAbout">
                    <h1>How it works</h1>
                    <p>Lorem Ipsum has been the industry's standard dummy text .</p>
                </div>
                <div id="bottomAbout">
                    <div className="card left">
                        <img src={cardPic} alt="gambar" className="cardImg"></img>
                        <h2>Information</h2>
                        <p>Lorem Ipsum has been the industry's standard</p>
                    </div>
                    <div className="card mid">
                        <img src={cardPic} alt="gambar 2" className="cardImg"></img>
                        <h2>Data Secure</h2>
                        <p>Lorem Ipsum has been the industry's standard</p>
                    </div>
                    <div className="card right">
                        <img src={cardPic} alt="gambar 3" className="cardImg"></img>
                        <h2>Cards</h2>
                        <p>Lorem Ipsum has been the industry's standard</p>
                    </div>
                </div>
                <hr/>
            </section>
        )
    }
};

export default About;

