import React from "react";
import phonePic from "../../assets/final_phone.png";
import "./work.css";

class Work extends React.Component{
    render(){
        return(
            <section id="workSection">
                <div id="downloadSection">
                    <img src={phonePic} alt="Mockup pic" className="phonePic"></img>
                    <div class="textContainer">
                        <h1>Download</h1>
                        <p>Lorem Ipsum has been the industry's standard dummy text ever since the 1500s</p>
                        <button type="whiteButton" className="whiteButton"><i class="fab fa-app-store"></i>App Store</button>
                        <button type="whiteButton" className="whiteButton"><i class="fab fa-google-play"></i>Play Store</button>
                    </div>
                </div>
                <div id="connectingSection">
                    <div class="textContainer">
                        <h1>Connecting all the banks needs</h1>
                        <p>Lorem Ipsum has been the industry's standard dummy text ever since the 1500s</p>
                        <button type="whiteButton" className="whiteButton">Get Started</button>
                    </div>
                    <img src={phonePic} alt="Mockup pic" className="phonePic"></img>
                </div>
            </section>
        )
    }
};

export default Work;

