import React from "react";
import './header.css';
import RLogo from "../../logo.svg";

class Header extends React.Component{
    render(){
        return(
            <header >
                <img src={RLogo} alt="logo"></img>
                <nav>
                    <ul id="nav-bar">
                        <li><a href="#homeSection">Business</a></li>
                        <li><a href="#aboutSection">Pricing</a></li>
                        <li><a href="#workSection">Features</a></li>
                        <li><a href="#contactSection">About</a></li>
                        {/* <li>Log in</li>
                        <li>Sign up</li> */}
                    </ul>
                    

                    

                </nav>

            <ul id='logSign'>
                <li>Log in </li>
                <button class="button signUp">Sign up</button>
            </ul>
            </header>
        )
    }
};



export default Header;

